    import React, { useState, useEffect } from 'react';
    import { Link } from 'react-router-dom';
    import Modal from 'react-modal';
    import '../MyPage/css/myPage.css'; // CSS  import

    const MyPage = () => {
        const [userNickname, setUserNickname] = useState('');
        const [profile, setProfile] = useState('');
        const [coursesKey, setCoursesKey] = useState('');
        const [profileImagePath, setProfileImagePath] = useState('');
        const [showDeleteModal, setShowDeleteModal] = useState(false); //   

        useEffect(() => {
            setUserNickname(sessionStorage.getItem('userNickname') || '');
            setProfile(sessionStorage.getItem('profile') || '');
            setCoursesKey(sessionStorage.getItem('coursesKey') || '');
        }, []); 

        useEffect(() => {
            if (profile) {
                setProfileImagePath(profile.replace('src/main/reactfront/public', process.env.PUBLIC_URL));
            }
        }, [profile]); 

        const handleDelete = async () => {
            try {
                
                const deleteUrl = '/api/delete';
        
               
                const userId = sessionStorage.getItem('userId');
        
                
                const formData = new FormData();
                formData.append('user_pk', userId);
        
               
                const response = await fetch(deleteUrl, {
                    method: 'DELETE',
                    body: formData,
                });
        
                
                if (response.ok) {
                    console.log('  .');
                    setShowDeleteModal(false); 

                    sessionStorage.removeItem('userId'); 
                    sessionStorage.removeItem('userEmail');
                    sessionStorage.removeItem('userNickname');
                    sessionStorage.removeItem('platform');
                    sessionStorage.removeItem('isAdmin');
                    sessionStorage.removeItem('coursesKey');
                    sessionStorage.removeItem('profile');
                    window.location.href='/';


                } else {
                    
                    console.error('    :', response.statusText);
                }
            } catch (error) {
                console.error('    :', error);
            }
            



        };

        return (
            <div className="mypage-container">
                <div className="mypage-content">
                    <p> : {userNickname}</p>
                    <p>: {sessionStorage.getItem('userEmail')}</p>
                    <p className="key-icon">: {coursesKey !== null && <img src={coursesKey === '1' ? 'https://cdn-icons-png.flaticon.com/512/5883/5883300.png' : 'https://cdn-icons-png.flaticon.com/512/2089/2089784.png'} alt="Key Icon"/>}</p>
                    <p className="profile-image"> :<img src={profileImagePath} alt="Profile Image"/></p>
                    <div className="button-container">
                        <Link to="/edit-profile" className="edit-button"></Link>
                        <button className="delete-button" onClick={() => setShowDeleteModal(true)}></button>
                    </div>
                </div>
                {/*   */}
                <Modal
                    isOpen={showDeleteModal}
                    onRequestClose={() => setShowDeleteModal(false)}
                    className="modal"
                    overlayClassName="overlay"
                >
                    <h2> </h2>
                    <p> ?</p>
                    <button onClick={handleDelete} className="yes-button"></button>
                    <button onClick={() => setShowDeleteModal(false)} className="no-button"></button>
                </Modal>
            </div>
        );
    };

    export default MyPage;